
































































































































































































































































































































#QualityAssessment {
  padding: 10px;
  width: 100%;
  height: 100%;
  .searchesBox {
    width: 49%;
    overflow: hidden;
    .ivu-form-item {
      margin-bottom: 0;
      float: left;
    }
    margin-bottom: 10px;
  }
  .assessmentBox {
    width: 100%;
    height: calc(100% - 42px);
    overflow: hidden;
    .left {
      float: left;
      width: 49%;
      margin-left: 5px;
      height: calc(100% - 5px);
    }
    .right {
      position: relative;
      float: right;
      width: 49%;
      height: calc(100% - 5px);
      margin-right: 5px;
    }
  }
  .exportBtn {
    background: #2d579b;
    border-color: #2d579b;
    &:hover {
      background: #1a3a6f;
      border-color: #1a3a6f;
    }
  }
  .queryBtns {
    float: right;
    margin-top: 6px;
  }
  .detailsTableMap {
    height: 100%;
    width: 100%;
    position: absolute;
    right: -120%;
    top:0;
    z-index: 1356;
    background: #fff;
    transition: all 0.8s ease;
    border-radius: 0 0 5px 5px;
    .closeBtn{
      position: absolute;
      right:10px;
      bottom:10px;
      z-index: 131531313;
    }
  }
  .addActive{
    right:0;
  }
  .btn-add {
    background-color: #01c296;
    border-color: #01c296;
    &:hover, &:focus {
      background-color: #05ab85;
      border-color: #05ab85;
    }
  }
  .detailsTableMap2 {
    height: 100%;
    width: 50%;
    position: absolute;
    bottom: -120%;
    left: 0;
    padding: 15px;
    z-index: 1356;
    background: #fff;
    transition: all 0.8s ease;
    border-radius: 0 0 5px 5px;
    .closeBtn{
      position: absolute;
      right:10px;
      bottom:10px;
      z-index: 131531313;
    }
  }
  .desActive{
    bottom:0;
  }
}
